import { IonButtons, IonContent, IonFab, IonFabButton, IonHeader, IonIcon, IonItem, IonLabel, IonList, IonMenuButton, IonPage, IonSkeletonText, IonTitle, IonToolbar, useIonModal } from '@ionic/react';
import { add } from 'ionicons/icons';
import { useContext, useEffect } from 'react';
import { CampaignStatus, ICampaign } from '../../model/campaigns';
import { AppContext } from '../../store';
import { getCampaignsSuccess } from '../../store/campaigns/actions';
import Campaign from '../../components/Campaign';

const CampaignsPage: React.FC = () => {

  const { state, dispatch } = useContext(AppContext);
  const [present, dismiss] = useIonModal(Campaign, {
    campaign: undefined,
    onDismiss: handleDismiss,
    onSave: handleSave,
  });

  function handleDismiss() {
    dismiss();
  }

  function handleAdd() {
    present();
  }

  function handleSave(campaign: ICampaign)   {
    console.log('handleSave', campaign);
        // campaignsService('addCampaigns')
    //   .then((campaigns: ICampaign[]) => {
    //     dispatch(addCampaignsSuccess(campaigns));
    //   })
    dismiss();
  }

  useEffect(() => {
    if (state.campaigns) {
      return;
    }

    const campaigns: ICampaign[] = [{
      id: '1',
      name: 'Campaign 1',
      description: 'Description 1',
      startDate: new Date().getTime(),
      endDate: new Date().getTime(),
      status: CampaignStatus.Active,
      createdAt: new Date().toISOString(),
      events: ['1', '2'],
      audience: 'Audience 1',
      components: ['1', '2']
    }];
    setTimeout(() => {
      dispatch(getCampaignsSuccess(campaigns));
    }, 5000);

    // campaignsService('getCampaigns')
    //   .then((campaigns: ICampaign[]) => {
    //     dispatch(getCampaignsSuccess(campaigns));
    //   })
  }, [state.campaigns]);

  const items = !state.campaigns?.loading && state.campaigns?.loaded ? state.campaigns.campaigns?.map((campaign: ICampaign) => {
    return (
      <IonItem key={campaign.id} detail>
        <IonLabel>
          <h2>{campaign.name}</h2>
          <p>{campaign.description}</p>
        </IonLabel>
        <IonLabel slot="end" class="ion-padding-end">{campaign.status}</IonLabel>
      </IonItem>
    );
  }) : Array.from(Array(20).keys()).map((idx) => (
    <IonItem key={idx}>
      <IonLabel>
        <h2><IonSkeletonText animated style={{ width: '30%' }} /></h2>
        <p><IonSkeletonText animated style={{ width: '60%' }} /></p>
      </IonLabel>
      <IonLabel slot="end" class="ion-padding-end">
        <IonSkeletonText animated style={{ width: '50px' }} />
      </IonLabel>
    </IonItem>
  ));

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle>Campaigns</IonTitle>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonHeader collapse="condense">
          <IonToolbar>
            <IonTitle size="large">Campaigns</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonList>
          {items}
        </IonList>
        <IonFab vertical="bottom" horizontal="end" slot="fixed" onClick={handleAdd}>
          <IonFabButton>
            <IonIcon icon={add} />
          </IonFabButton>
        </IonFab>
      </IonContent>
    </IonPage>
  );
};

export default CampaignsPage;
