import {
    IonItem,
    IonLabel,
    IonInput,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonButtons,
    IonButton,
    IonContent,
    IonList,
    IonText,
    IonPopover,
    IonDatetime,
} from "@ionic/react";
import { parseISO, formatISO, format } from "date-fns";
import { useContext, useState } from "react";
import { ICampaign } from "../model/campaigns";
import { AppContext } from "../store";

const Campaign: React.FC<{
    campaign: ICampaign;
    error: any;
    loading: boolean;
    onDismiss: () => void;
    onSave: (campaign: ICampaign) => void;
}> = ({ campaign, error, loading, onDismiss, onSave }) => {
    const { state, dispatch } = useContext(AppContext);
    const [campaignState, setCampaignState] = useState(campaign ?? {
        startDate: Date.now(),
        endDate: Date.now(),
    });

    const setStartDate = (value: string) => {
        setCampaignState({
            ...campaignState,
            startDate: parseISO(value).valueOf(),
        });
    }
    const setEndDate = (value: string) => {
        setCampaignState({
            ...campaignState,
            endDate: parseISO(value).valueOf(),
        });
    }

    const id = campaignState.id ? (
        <IonItem>
            <IonLabel position="floating">ID</IonLabel>
            <IonInput readonly value={campaignState.id}></IonInput>
        </IonItem>
    ) : null;

    const title = campaignState.name ? `${campaignState.name} Campaign` : 'New Campaign';

    return (
        <div>
            <IonHeader>
                <IonToolbar>
                    <IonTitle>{title}</IonTitle>
                    <IonButtons slot="end">
                        <IonButton onClick={onDismiss}>Close</IonButton>
                    </IonButtons>
                </IonToolbar>
            </IonHeader>
            <IonContent fullscreen class="ion-padding">
                <IonList>
                    {id}
                    <IonItem>
                        <IonLabel position="floating">Name</IonLabel>
                        <IonInput value={campaignState.name}></IonInput>
                    </IonItem>
                    <IonItem>
                        <IonLabel position="floating">Description</IonLabel>
                        <IonInput value={campaignState.description}></IonInput>
                    </IonItem>
                    <IonItem button={true} id="start-date-input">
                        <IonLabel>Start Date</IonLabel>
                        <IonText slot="end">{format(campaignState.startDate, 'MMM dd yyyy')}</IonText>
                        <IonPopover trigger="start-date-input" showBackdrop={false}>
                            <IonDatetime
                                presentation="date"
                                min={formatISO(Date.now())}
                                max={formatISO(campaignState.endDate)}
                                value={formatISO(campaignState.startDate)}
                                onIonChange={ev => setStartDate(ev.detail.value!)}
                            />
                        </IonPopover>
                    </IonItem>
                    <IonItem button={true} id="end-date-input">
                        <IonLabel>End Date</IonLabel>
                        <IonText slot="end">{format(campaignState.endDate, 'MMM dd yyyy')}</IonText>
                        <IonPopover trigger="end-date-input" showBackdrop={false}>
                            <IonDatetime
                                presentation="date"
                                min={formatISO(campaignState.startDate)}
                                value={formatISO(campaignState.endDate)}
                                onIonChange={ev => setEndDate(ev.detail.value!)}
                            />
                        </IonPopover>
                    </IonItem>
                </IonList>
                <IonButton expand="block" onClick={() => onSave(campaignState)} disabled={state.campaigns?.loading}>
                    Save
                </IonButton>
            </IonContent>
        </div>
    );
};
export default Campaign;
