/**
 * 
 */
export enum CampaignStatus {
    Draft = 'Draft',
    Active = 'Active',
    Paused = 'Paused',
    Completed = 'Completed',
    Cancelled = 'Cancelled',
}

/**
 * 
 */
export interface ICampaign {
    id: string;
    name: string;
    description: string;
    startDate: number;
    endDate: number;
    status: CampaignStatus;
    createdAt: string;
    events: string[];
    audience: string;
    components: string[];
};