import { ICampaign } from "../../model/campaigns";
import { IAction } from "../action";
import { ActionTypes } from "./actions";
import { ICampaignState, initialState } from "./state";

const getCampaigns = (state: ICampaignState, action: IAction): ICampaignState => ({
    ...state,
    error: undefined,
    loading: true,
});

const getCampaignsSuccess = (state: ICampaignState, action: IAction<ICampaign[]>): ICampaignState => ({
    ...state,
    loading: false,
    loaded: true,
    campaigns: action.payload,
});

const getCampaignsFailure = (state: ICampaignState, action: IAction): ICampaignState => ({
    ...state,
    loading: false,
    loaded: false,
    error: action.payload,
});

const addCampaign = (state: ICampaignState, action: IAction<ICampaign>): ICampaignState => ({
    ...state,
    error: undefined,
    loading: true,
});

const addCampaignSuccess = (state: ICampaignState, action: IAction<ICampaign>): ICampaignState => {
    const campaigns = state.campaigns || [];
    if (action.payload) {
        campaigns.push(action.payload);
    }
    return {
        ...state,
        loading: false,
        loaded: true,
        campaigns,
    }
};

const addCampaignFailure = (state: ICampaignState, action: IAction): ICampaignState => ({
    ...state,
    loading: false,
    loaded: false,
    error: action.payload,
});

const reducers = {
    [ActionTypes.GET_CAMPAIGNS]: getCampaigns,
    [ActionTypes.GET_CAMPAIGNS_SUCCESS]: getCampaignsSuccess,
    [ActionTypes.GET_CAMPAIGNS_FAILURE]: getCampaignsFailure,
    [ActionTypes.ADD_CAMPAIGN]: addCampaign,
    [ActionTypes.ADD_CAMPAIGN_SUCCESS]: addCampaignSuccess,
    [ActionTypes.ADD_CAMPAIGN_FAILURE]: addCampaignFailure,
};

export const campaignsReducer = (state = initialState, action: IAction) => {
    return reducers[action.type] ? reducers[action.type](state, action) : state;
};
