import { ICampaign } from "../../model/campaigns";
import { IAction } from "../action";

export const ActionTypes = {
    GET_CAMPAIGNS: 'GET_CAMPAIGNS',
    GET_CAMPAIGNS_SUCCESS: 'GET_CAMPAIGNS_SUCCESS',
    GET_CAMPAIGNS_FAILURE: 'GET_CAMPAIGNS_FAILURE',
    ADD_CAMPAIGN: 'ADD_CAMPAIGN',
    ADD_CAMPAIGN_SUCCESS: 'ADD_CAMPAIGN_SUCCESS',
    ADD_CAMPAIGN_FAILURE: 'ADD_CAMPAIGN_FAILURE',
};

export const getCampaigns = (): IAction => ({
    type: ActionTypes.GET_CAMPAIGNS,
});

export const getCampaignsSuccess = (campaigns: ICampaign[]): IAction<ICampaign[]> => ({
    type: ActionTypes.GET_CAMPAIGNS_SUCCESS,
    payload: campaigns,
});

export const getCampaignsFailure = (error: Error): IAction<Error> => ({
    type: ActionTypes.GET_CAMPAIGNS_FAILURE,
    payload: error,
});

export const addCampaign = (campaign: ICampaign): IAction<ICampaign> => ({
    type: ActionTypes.ADD_CAMPAIGN,
    payload: campaign,
});

export const addCampaignSuccess = (campaign: ICampaign): IAction<ICampaign> => ({
    type: ActionTypes.ADD_CAMPAIGN_SUCCESS,
    payload: campaign,
});

export const addCampaignFailure = (error: Error): IAction<Error> => ({
    type: ActionTypes.ADD_CAMPAIGN_FAILURE,
    payload: error,
});
